module.exports = "import React from 'react';\nimport { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';\n\nexport default class Example extends React.Component {\n  render() {\n    return (\n      <Form>\n        <FormGroup>\n          <Label for=\"exampleEmail\">Static</Label>\n          <Input static>Some static value</Input>\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleEmail\">Email</Label>\n          <Input type=\"email\" name=\"email\" id=\"exampleEmail\" placeholder=\"with a placeholder\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"examplePassword\">Password</Label>\n          <Input type=\"password\" name=\"password\" id=\"examplePassword\" placeholder=\"password placeholder\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleUrl\">Url</Label>\n          <Input type=\"url\" name=\"url\" id=\"exampleUrl\" placeholder=\"url placeholder\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleNumber\">Number</Label>\n          <Input type=\"number\" name=\"number\" id=\"exampleNumber\" placeholder=\"number placeholder\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleDatetime\">Datetime</Label>\n          <Input type=\"datetime\" name=\"datetime\" id=\"exampleDatetime\" placeholder=\"datetime placeholder\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleDate\">Date</Label>\n          <Input type=\"date\" name=\"date\" id=\"exampleDate\" placeholder=\"date placeholder\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleTime\">Time</Label>\n          <Input type=\"time\" name=\"time\" id=\"exampleTime\" placeholder=\"time placeholder\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleColor\">Color</Label>\n          <Input type=\"color\" name=\"color\" id=\"exampleColor\" placeholder=\"color placeholder\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleSearch\">Search</Label>\n          <Input type=\"search\" name=\"search\" id=\"exampleSearch\" placeholder=\"search placeholder\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleSelect\">Select</Label>\n          <Input type=\"select\" name=\"select\" id=\"exampleSelect\">\n            <option>1</option>\n            <option>2</option>\n            <option>3</option>\n            <option>4</option>\n            <option>5</option>\n          </Input>\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleSelectMulti\">Select Multiple</Label>\n          <Input type=\"select\" name=\"selectMulti\" id=\"exampleSelectMulti\" multiple>\n            <option>1</option>\n            <option>2</option>\n            <option>3</option>\n            <option>4</option>\n            <option>5</option>\n          </Input>\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleText\">Text Area</Label>\n          <Input type=\"textarea\" name=\"text\" id=\"exampleText\" />\n        </FormGroup>\n        <FormGroup>\n          <Label for=\"exampleFile\">File</Label>\n          <Input type=\"file\" name=\"file\" id=\"exampleFile\" />\n          <FormText color=\"muted\">\n            This is some placeholder block-level help text for the above input.\n            It's a bit lighter and easily wraps to a new line.\n          </FormText>\n        </FormGroup>\n        <FormGroup check>\n          <Label check>\n            <Input type=\"radio\" />{' '}\n            Option one is this and that—be sure to include why it's great\n          </Label>\n        </FormGroup>\n        <FormGroup check>\n          <Label check>\n            <Input type=\"checkbox\" />{' '}\n            Check me out\n          </Label>\n        </FormGroup>\n      </Form>\n    );\n  }\n}\n"


